import { g } from './common'
/**
 * Chapter7 城镇长距离供水输水管道水力学计算
 * 部分计算参考以下网址
 * https://raymondmcguire.github.io/?e=HYCAL&&theme=Image-Visual-Lab
 */
export module Chapter7 {

    /**
     * 7.1 塑料管道沿程水头损失（达西公式）
     * @param u
     * @param d_j
     * @param v
     * @param l
     */
    export function slgdyctss(u:number, d_j:number, v:number, l:number) {
      let R_e = u * d_j / v
      let lambda = 0.25 / Math.pow(R_e, 0.226)
      let h_y = lambda * l * u * u / (d_j * 2 * g)
      // console.log(R_e);
      // console.log(lambda);
      // console.log(h_y);
      return h_y
    }

    /**
     * 7.2 混凝土管（渠）及采用水泥砂浆内衬的金属管道（谢才公式）
     * @param d_j
     * @param n
     * @param u
     * @param l
     */
    export function hntgjsgd(d_j:number, n:number, u:number, l:number) {
      let R = d_j / 4
      let C = Math.pow(R, 1 / 6) / n
      let h_y = u * u * l / (C * C * R)
      let i = h_y / l
      // console.log(R);
      // console.log(C);
      // console.log(h_y);
      // console.log(i);
      return [h_y, i]
    }

    /**
     * 7.3 输配水管道、配水管网水力平差计算（海曾—威廉公式）
     * @param q
     * @param d_j
     * @param C_h
     * @param l
     */
    export function spsgd(q:number, d_j:number, C_h:number, l:number) {
      let h_y = 10.67 * Math.pow(q, 1.852) * l / (Math.pow(C_h, 1.852) * Math.pow(d_j, 4.87))
      let i = h_y / l
      // console.log(h_y);
      // console.log(i);
      return [h_y, i]
    }

    /**
     * 7.4 管（渠）道局部损失
     * @param v
     * @param n_j
     * @param ksi_j
     * @param h_y
     */
    export function gdjbss(v:number, n_j:Array<number>, ksi_j:Array<number>, h_y:number) {
      let h_j = 0
      for (let index = 0; index < n_j.length; index++) {
        h_j += n_j[index] * ksi_j[index]
      }
      h_j *= v * v / (2 * g)
      // console.log(h_j);
      let h_j_d = h_y * 0.1
      return [h_j, h_j_d]
    }

    /**
     * 7.5 管（渠）道总水头损失
     * @param h_y
     * @param h_j
     */
    export function gdzstss(h_y:number, h_j:number) {
      let h_z = h_y + h_j
      return h_z
    }
}
